import React, { useState } from 'react';
import '../../../componets/log-in/login.css'; // reuse same styling
import { FiArrowRight, FiCheckCircle } from 'react-icons/fi';

const i18n = {
  EN: {
    brandTitle: 'TINH HOA VI·ªÜT',
    tagline: 'Where Vietnamese Excellence Meets Global Recognition',
    features: ['Exclusive Gala Event', 'Cultural Performances', 'Networking with Honorees'],
    title: 'Event Ticket Payment',
    subtitle: 'Complete your payment to confirm your attendance',
    eventName: 'Vietnamese Excellence Gala 2025',
    date: 'December 10, 2025 ‚Äì 7:00 PM',
    location: 'Saigon Opera House, Ho Chi Minh City',
    price: 'Ticket Price: 500,000‚Ç´',
    payBtn: 'Pay Now',
    loading: 'Processing payment...',
    success: 'Payment Confirmed! Thank you.',
    langEN: 'EN',
    langVN: 'VN'
  },
  VN: {
    brandTitle: 'TINH HOA VI·ªÜT',
    tagline: 'N∆°i t√¥n vinh tinh hoa Vi·ªát Nam v∆∞∆°n t·∫ßm qu·ªëc t·∫ø',
    features: ['S·ª± ki·ªán Gala ƒë·ªôc quy·ªÅn', 'Bi·ªÉu di·ªÖn vƒÉn h√≥a', 'K·∫øt n·ªëi c√πng c√°c tinh hoa'],
    title: 'Thanh To√°n V√© S·ª± Ki·ªán',
    subtitle: 'Ho√†n t·∫•t thanh to√°n ƒë·ªÉ x√°c nh·∫≠n tham d·ª±',
    eventName: 'Gala Tinh Hoa Vi·ªát 2025',
    date: '10/12/2025 ‚Äì 19:00',
    location: 'Nh√† h√°t Th√†nh ph·ªë, TP. H·ªì Ch√≠ Minh',
    price: 'Gi√° v√©: 500,000‚Ç´',
    payBtn: 'Thanh to√°n ngay',
    loading: 'ƒêang x·ª≠ l√Ω thanh to√°n...',
    success: 'Thanh to√°n th√†nh c√¥ng! C·∫£m ∆°n b·∫°n.',
    langEN: 'EN',
    langVN: 'VN'
  }
};

const EventPayment: React.FC = () => {
  const [language, setLanguage] = useState<'EN' | 'VN'>('EN');
  const [loading, setLoading] = useState(false);
  const [isPaid, setIsPaid] = useState(false);
  const t = i18n[language];

  const handlePayment = async () => {
    const endpoint = 'https://integrations.tinhhoaviet.org.vn/vietqr/payment-intent';
    const proxy = 'https://corsproxy.io/?';

    try {
      setLoading(true);

      // Try direct call first
      let res = await fetch(endpoint, { method: 'GET' });
      if (!res.ok) throw new Error('Direct call failed (CORS or network)');
      let data = await res.json();

      console.log('üí≥ VietQR API Response:', data);

      if (data.qrLink) {
        //window.location.href = data.qrLink;
        window.open(data.qrLink, '_blank');
        return;
      }

      // fallback: retry via proxy if qrLink missing or blocked
      console.warn('‚ö†Ô∏è Retrying through proxy...');
      res = await fetch(proxy + encodeURIComponent(endpoint));
      if (!res.ok) throw new Error('Proxy call failed');
      data = await res.json();

      console.log('üí≥ VietQR API Response (via proxy):', data);
      if (data.qrLink) {
        //window.location.href = data.qrLink;
        window.open(data.qrLink, '_blank');
      } else {
        alert('Payment link not received.');
        setIsPaid(true);
      }
    } catch (err) {
      console.error('‚ùå Payment Error:', err);
      alert('An error occurred while initiating payment. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="auth-shell">
      {/* Left branding panel */}
      <aside className="brand-side">
        <div className="brand-sheen" />
        <div className="brand-pattern" />
        <div className="brand-wrap">
          <h1 className="brand-name">{t.brandTitle}</h1>
          <p className="brand-tag">{t.tagline}</p>
          <ul className="brand-features">
            {t.features.map((f, i) => (
              <li key={i} className="feature">
                <span className="tick">‚úì</span><span>{f}</span>
              </li>
            ))}
          </ul>
        </div>
      </aside>

      {/* Right panel */}
      <main className="form-side">
        <header className="form-top">
          <div className="lang-switch">
            <button
              className={`lang ${language === 'EN' ? 'active' : ''}`}
              onClick={() => setLanguage('EN')}
            >{t.langEN}</button>
            <span className="mid">|</span>
            <button
              className={`lang ${language === 'VN' ? 'active' : ''}`}
              onClick={() => setLanguage('VN')}
            >{t.langVN}</button>
          </div>
        </header>

        <section className="form-wrap" style={{ color: 'var(--ink)' }}>
          <div className="intro">
            <h2 className="title">{t.title}</h2>
            <p className="subtitle">{t.subtitle}</p>
          </div>

          <div className="auth-form" style={{ textAlign: 'center', color: 'var(--ink)' }}>
            <div className="field">
              <h3 style={{ color: 'var(--gold-2)', fontSize: 22, marginBottom: 6 }}>{t.eventName}</h3>
              <p>{t.date}</p>
              <p>{t.location}</p>
              <p style={{ fontWeight: 700, marginTop: 10 }}>{t.price}</p>
            </div>

            {!isPaid ? (
              <button
                className="cta"
                onClick={handlePayment}
                disabled={loading}
                style={{ opacity: loading ? 0.7 : 1 }}
              >
                <span>{loading ? t.loading : t.payBtn}</span>
                {!loading && <FiArrowRight className="arrow" />}
              </button>
            ) : (
              <div style={{
                marginTop: 20,
                color: 'var(--gold-2)',
                fontWeight: 700,
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                gap: 8
              }}>
                <FiCheckCircle size={22} />
                {t.success}
              </div>
            )}
          </div>
        </section>
      </main>
    </div>
  );
};

export default EventPayment;
